<template>
  <ul class="card p30">
    <li v-for="item in cList" :key="item.id" :item="item">
      <div class="alarmListItem pr">
        <div class="symbolBox">
          <img :src="item.coinUrl" style='width:100%' alt="coin ico"/>
        </div>
        <div class="coinBox">
          <div class="name">{{item.name}}</div>
          <div>{{item.coin.toUpperCase()}}</div>
        </div>
        <div class="numberBox" :class="{'lightGreen':item.amount>0,'lightRed':item.amount<0}">{{item.amount<0?'sell':'buy'}}</div>
        <div class="grey amBox"><span class="label">amount: </span>{{item.amount | numFormat('0,0.000')}}</div>
        <div><span class="grey label">price: </span>{{item.price | numFormat('$ 0,0.00')}}</div>
        <div class="prBox"><span class="grey">= {{(item.price*item.amount) | numFormat('$ 0,0.00')}}</span></div>
        <div class="prBox curr"><span class="grey label">cPrice: </span>{{item.currentPrice| numFormat('$ 0,0.000')}}</div>
        <div class="dateBox"><span class="grey label">date: </span>{{item.date}}</div>
        <span class="grey label">comment: </span>
        <form @submit.prevent="update">
          <input type="text" v-model="item.comment" class="inputBox" v-on:keyup.enter="(ev) => ev.target.blur()">
        </form>
        <button @click="deleteItem(item.id)" class="delBox">x</button>
      </div>
    </li>
  </ul>
</template>

<script>
  export default {
    props:['list'],
    computed: {
      cList() {
        if (this.list && this.list.length) {
          return this.list
        } else return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].lastActivityList
      },
    },
    methods: {
      deleteItem(id){
        this.$store.commit('deleteItem', { id, listName:'lastActivityList'})
      },
      update(){
        this.$store.dispatch('updateFB',{name:'lastActivityList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
    },
  };
</script>

