<template>
  <section>
    <div @click="isActivityPanelOpen=!isActivityPanelOpen" class="hover">
      <div class="fc" v-if="isActivityPanelOpen">
        <div class="card p5">Last action opened</div>
      </div>
      <div v-if="!isActivityPanelOpen">
        <SumGlobal v-if="$route.name==='main'||$route.name==='reserve'" @click="isActivityPanelOpen=!isActivityPanelOpen"/>
      </div>
    </div>
    <SumMain :list="cList"/>
    <AltsList :list="cList" v-if="!isActivityPanelOpen"/>
    <SubTitle title="Last actions" v-if="$route.name==='main' && isActivityPanelOpen" />
    <ActivityList v-if="isActivityPanelOpen"/>

  </section>
</template>

<script>
  import AltsList from '../../components/AltsList/AltsList';
  import SumMain from '../../components/sum/SumMain';
  import SumGlobal from '../../components/sum/SumGlobal';
  import SubTitle from '../../components/SubTitle';
  import ActivityList from '../../components/ActivityList';
  import HypSide from '../../components/HypSide';
  export default {
    props:['list'],
    components: {
      HypSide,
      ActivityList,
      SubTitle,
      SumMain,
      SumGlobal,
      AltsList,
    },
    data() {
      return {
        isActivityPanelOpen:false,
        isHypPanelOpen:false,
      };
    },
    computed: {
      cList(){
        if(this.$route.name==='main'){
          return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].tradeList
        }else return this.list
      },
    },
  }
</script>
<style scoped>
  @media only screen and (max-width: 768px) {
  }
</style>
